html {
  height: 100%;
}

body {
  min-height: 100%;
}

#app {
  min-height: 100vh;
}
#content {
    padding-top: 70px;
    min-height: "calc(100vh - 92px)";
  }
  .navbar,
  .navbar-default {
      background-image: -webkit-gradient(linear,left top, left bottom,color-stop(0, #2c4053),to(#2c4053)) !important;
      background-image: -webkit-linear-gradient(top,#2c4053 0,#2c4053 100%) !important;
      background-image: -o-linear-gradient(top,#2c4053 0,#2c4053 100%) !important;
      background-image: linear-gradient(to bottom,#2c4053 0,#2c4053 100%) !important; /* override background image gradient w/ flat color */
  }
  .nav.navbar-nav.navbar-right li a {
    color: white;
}
.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  text-align: center;
  background-image: -webkit-gradient(linear,left top, left bottom,color-stop(0, #2c4053),to(#2c4053)) !important;
  background-image: -webkit-linear-gradient(top,#2c4053 0,#2c4053 100%) !important;
  background-image: -o-linear-gradient(top,#2c4053 0,#2c4053 100%) !important;
  background-image: linear-gradient(to bottom,#2c4053 0,#2c4053 100%) !important; /* override background image gradient w/ flat color */
  font-size: 1em;
  color: white;
}
.form-horizontal .control-label{
  text-align: left;
}
span.underline { 
  color: red; 
  text-decoration: underline; 
} 
span.underline a { 
  color: white; 
  text-decoration: none; 
} 
.navbar-header .navbar-brand {
  height: 60px;
  float: left;
}
.profileImg{
  height: 30px;
  width: 30px;
  border-radius: 50%;
}




