@media all and (min-width: 480px) {
    .Login {
      padding: 60px 0;
    }
  
    .Login form {
      margin: 0 auto;
      max-width: 320px;
    }
  }
  .loginfooter {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    text-align: center;
    font-size: 1em;
    color: black;
  }
  .detImg{
    height: auto;
    width: auto;
    max-height: 200px;
    max-width: 100%;
  }


